import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { io, Socket } from 'socket.io-client';
import { useAuth } from './useAuth';
import { Package } from '../types';

interface SocketContextType {
  socket: Socket | null;
  connected: boolean;
  subscribeToTracking: (trackingNumber: string) => void;
  unsubscribeFromTracking: (trackingNumber: string) => void;
}

const SocketContext = createContext<SocketContextType | undefined>(undefined);

interface SocketProviderProps {
  children: ReactNode;
}

export const SocketProvider: React.FC<SocketProviderProps> = ({ children }) => {
  const [socket, setSocket] = useState<Socket | null>(null);
  const [connected, setConnected] = useState(false);
  const { user, isAuthenticated } = useAuth();

  useEffect(() => {
    if (isAuthenticated && user) {
      const token = localStorage.getItem('accessToken');
      if (!token) return;

      const socketInstance = io(process.env.REACT_APP_SERVER_URL || 'http://localhost:5000', {
        auth: {
          token,
        },
        transports: ['websocket'],
      });

      socketInstance.on('connect', () => {
        console.log('Connected to server');
        setConnected(true);
      });

      socketInstance.on('disconnect', () => {
        console.log('Disconnected from server');
        setConnected(false);
      });

      socketInstance.on('connect_error', (error) => {
        console.error('Socket connection error:', error);
        setConnected(false);
      });

      // Listen for package updates
      socketInstance.on('package:update', (packageData: Package) => {
        console.log('Package update received:', packageData);
        // Emit custom event for components to listen to
        window.dispatchEvent(new CustomEvent('packageUpdate', { detail: packageData }));
      });

      // Listen for new packages
      socketInstance.on('package:new', (packageData: Package) => {
        console.log('New package received:', packageData);
        // Emit custom event for components to listen to
        window.dispatchEvent(new CustomEvent('newPackage', { detail: packageData }));
      });

      setSocket(socketInstance);

      return () => {
        socketInstance.disconnect();
        setSocket(null);
        setConnected(false);
      };
    }
  }, [isAuthenticated, user]);

  const subscribeToTracking = (trackingNumber: string) => {
    if (socket) {
      socket.emit('subscribe:tracking', trackingNumber);
    }
  };

  const unsubscribeFromTracking = (trackingNumber: string) => {
    if (socket) {
      socket.emit('unsubscribe:tracking', trackingNumber);
    }
  };

  const value = {
    socket,
    connected,
    subscribeToTracking,
    unsubscribeFromTracking,
  };

  return (
    <SocketContext.Provider value={value}>
      {children}
    </SocketContext.Provider>
  );
};

export const useSocket = () => {
  const context = useContext(SocketContext);
  if (context === undefined) {
    throw new Error('useSocket must be used within a SocketProvider');
  }
  return context;
};
